.App {
  text-align: justify;
  display: flex;
  flex-direction: column;
  align-items: center;
}



.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

p{
  margin-bottom: 30px;
}

.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100svh;
  max-width: 500px;
  
}
.container-header{
  background-color: #606C38;
  border-radius: 15px;
  margin: 0 0.8rem ;

}

.container-button {
  background-color: #dda15e;
  color: #7b481a;
  font-family: Comic Sans MS, Comic Sans, cursive;
  border-radius: 10px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  margin: 1rem;
  border: none;
  width: 10rem;
  height: 3rem;
  transition: background-color 0.4s, color 0.3s;
  box-shadow: 0 0 10px #7b481a;
}

.container-button:hover {
  background-color: #7b481a;
  color: #dda15e;
  transition: background-color 0.4s, color 0.3s;
}

.container-text {
  font-family: Comic Sans MS, Comic Sans, cursive;
  color: #FEFAE0;
  margin: 1rem;
  
}

.container-footer{
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  background-color: #606C38;
  border-radius: 20px;

  margin: 0.6rem 0.8rem;
  margin-left: 0.8rem;
  
}

@media screen and (max-width: 400px) {

  img {
    width: 80vw;
    height: auto;
    

  }

}


.App-header {
  background-color: #FEFAE0;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
